*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: purple;
  border: 2px solid rgb(199, 197, 197);
  border-radius: 0.1em;
  background-color: white;
  font-size: 1.3rem;
  height: 2em;
  width: 99%;
}

.card{
  align-content: center;
  border: 3px solid purple;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap:wrap; 

}

.card img{
  max-height: 10em;
  
}

